              p Integra paisajes naturales, fincas agroturísticas, parques temáticos y pueblos patrimoniales bajo la marca “Paisaje Cultural Cafetero”, declarada Patrimonio Mundial por la UNESCO.

      .col-sm-6.col-xl-3.mb-4.mb-sm-0
